const SET_PROJECTs = 'projects/setProjects';
const CLEAR_PROJECTS = 'projects/clearProjects';

export default (state = false, action) => {
	let data;
	switch (action.type) {
		case SET_PROJECTs:
			data = [...action.payload];
			data.sort(data.id);
			console.log('sorted data: ', data);
			return [...data];
		case CLEAR_PROJECTS:
			return false;
		default:
			return state;
	}
};

const setProjects = (payload) => {
	return {
		type: SET_PROJECTs,
		payload: payload,
	};
};

const clearProjects = () => {
	return {
		type: CLEAR_PROJECTS,
	};
};

export { setProjects, clearProjects };
