import { useEffect } from 'react';
import { setContact } from '../Redux/Reducers/pages';
import useFetch from '../Hooks/fetch';
import { useDispatch } from 'react-redux';
import { setChangingPage, setMainPageLoaded } from '../Redux/Reducers/lifecycle';
import { useNavigate } from 'react-router-dom';

export default function useContact() {
	const { getData } = useFetch();
	const dispatch = useDispatch();
	const navigate = useNavigate();
	useEffect(() => {
		getData('/contact-pages/1?populate=*').then((res) => {
			if (!res.error) {
				dispatch(
					setContact({
						...res.data.attributes,
						image: { ...res.data.attributes.image.data.attributes },
						imageAvatar: { ...res.data.attributes.imageAvatar.data.attributes },
					})
				);
				dispatch(setMainPageLoaded());
				setTimeout(() => {
					dispatch(setChangingPage(false));
				}, 300);
				console.log('contact res: ', res);
			} else {
				navigate('/error');
			}
		});
	}, []);
	return;
}
