export default function useFetch() {
        const getData = async (path, body, method) => {
                let res = await fetch(`${process.env.REACT_APP_API_URL}${path}`, {
                        method: method ? method : 'GET',
                        headers: {
                                'Content-Type': 'application/json'
                        },
                        body: JSON.stringify(body)
                });
                res = await res.json();
                if (res.error) {
                        return res;
                } else {
                        return res;
                }
        };
        return { getData };
}
