import { useEffect } from 'react';
import useFetch from '../Hooks/fetch';
import { useDispatch } from 'react-redux';
import { setChangingPage, setMainPageLoaded } from '../Redux/Reducers/lifecycle';
import { setAbout } from '../Redux/Reducers/pages';
import { useNavigate } from 'react-router-dom';

export default function useAbout() {
	const { getData } = useFetch();
	const dispatch = useDispatch();
	const navigate = useNavigate();
	useEffect(() => {
		getData('/shousugibans/1?populate=*').then((res) => {
			if (!res.error) {
				dispatch(setAbout({ ...res.data.attributes, image: { ...res.data.attributes.image.data.attributes } }));
				dispatch(setMainPageLoaded());
				setTimeout(() => {
					dispatch(setChangingPage(false));
				}, 300);
				console.log('shou sugi ban res: ', res);
			} else {
				navigate('/error');
			}
		});
	}, []);
}
