import React from 'react';
import './index.css';
import useAbout from '../../Application/Controller/about';
import { useSelector } from 'react-redux';
import ReactMarkdown from 'react-markdown';

export default function About() {
	const about = useSelector((state) => state.pages.about);
	useAbout();
	return (
		<div className='about'>
			{about && (
				<div>
					<div className='about-image'>
						<img src={`${process.env.REACT_APP_BASE_URL}${about.image.url}`} />
					</div>
					<h1>{about.header}</h1>
					<ReactMarkdown skipHtml={false}>{about.text}</ReactMarkdown>
				</div>
			)}
		</div>
	);
}
