import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setFooterLoaded } from '../Redux/Reducers/lifecycle';
import { useLocation } from 'react-router-dom';
import { gsap } from 'gsap';

export default function useFooter(footerRef) {
	const dispatch = useDispatch();
	const [mw, setMw] = useState(false);
	const location = useLocation();
	const pageLoaded = useSelector((state) => state.lifecycle.pageLoaded);
	const changingPage = useSelector((state) => state.lifecycle.changingPage);
	useEffect(() => {
		if (window.location.pathname === '/products' || window.location.pathname.includes('project/') || window.location.pathname === '/about') {
			setMw(true);
		} else {
			setMw(false);
		}
	}, [location]);

	useEffect(() => {
		dispatch(setFooterLoaded());
	}, []);

	useEffect(() => {
		pageLoaded &&
			setTimeout(() => {
				gsap.to(footerRef.current, { opacity: 1, duration: 0.1 });
			}, 1000);
	}, [pageLoaded]);

	useEffect(() => {
		changingPage && console.log('page is loading');
		changingPage && gsap.set(footerRef.current, { opacity: 0 });
		!changingPage && gsap.to(footerRef.current, { opacity: 1, duration: 0.1, delay: 0.65 });
	}, [changingPage]);

	return { mw };
}
