import { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { setChangingPage, setheaderLoaded } from '../Redux/Reducers/lifecycle';
import { useNavigate } from 'react-router-dom';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
gsap.registerPlugin(ScrollTrigger);

export default function useHeader(headerRef) {
	const dispatch = useDispatch();
	const navigate = useNavigate();
	const [animated, setAnimated] = useState(false);

	const [showMobile, setShowMobile] = useState(false);
	useEffect(() => {
		setTimeout(() => {
			dispatch(setheaderLoaded());
		}, 300);
	}, []);

	const handleNavigate = (adress) => {
		if (window.location.pathname != adress) {
			dispatch(setChangingPage(true));
			navigate(adress);
		}
	};

	const showMobileNav = () => {
		setShowMobile(true);
	};

	const hideMobileNav = () => {
		setShowMobile(false);
	};

	// <<<<<<<<<<>>>>>>>>>>>>> HEADER SCROLL ANIMATION >>>>>>>>>>>><<<<<<<

	// useEffect(() => {
	// 	headerRef && console.log('headerRef kurwo');
	// 	headerRef &&
	// 		gsap.to(headerRef, {
	// 			minHeight: 80,
	// 			duration: 0.2,
	// 			ease: 'circ',
	// 			scrollTrigger: {
	// 				trigger: headerRef,
	// 				start: 'top top',
	// 				toggleActions: 'play none reset none',
	// 				preventOverlaps: true,
	// 			},
	// 		});
	// }, [headerRef]);

	return { handleNavigate, showMobile, showMobileNav, hideMobileNav };
}
