import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { setChangingPage, setMainPageLoaded } from '../Redux/Reducers/lifecycle';
import { useLocation, useNavigate } from 'react-router-dom';
import useFetch from '../Hooks/fetch';
import { clearProject, setProject } from '../Redux/Reducers/project';
import { clearProduct } from '../Redux/Reducers/product';

export default function useProject() {
	const dispatch = useDispatch();
	const location = useLocation();
	const navigate = useNavigate();
	const { getData } = useFetch();

	useEffect(() => {
		console.log(location.pathname.split('/')[2]);
		getData(`/projects/${location.pathname.split('/')[2]}?populate=*`).then((res) => {
			if (res.error || !res) {
				navigate('/error');
			} else {
				console.log('my res elo: ', res);
				dispatch(
					setProject({
						...res.data.attributes,
						id: res.data.id,
						image: res.data.attributes.image.data
							? { ...res.data.attributes.image.data.attributes, id: res.data.attributes.image.data.id }
							: null,
						gallery: res.data.attributes.gallery.data
							? res.data.attributes.gallery.data.map((el) => {
									return { ...el.attributes, id: el.id };
							  })
							: null,
					})
				);
				dispatch(setMainPageLoaded());
				setTimeout(() => {
					dispatch(setChangingPage(false));
				}, 300);
			}
		});

		return () => {
			dispatch(clearProject());
		};
	}, []);
}
