import React, { useEffect, useRef, useState } from 'react';
import './index.css';
import useHeader from '../../Application/Controller/header';
import NavElement from './navElement';
import MobileNav from './mobileNav';

export default function Header() {
	const menu = [
		{ name: 'fired wood', link: '/' },
		{ name: 'unsere produkte', link: '/products' },
		{ name: 'shou sugi ban', link: '/about' },
		{ name: 'contacten', link: '/contact' },
	];
	const ref = useRef(null);
	const [headerRef, setHeaderRef] = useState();

	useEffect(() => {
		setHeaderRef(ref.current);
	}, []);

	const { handleNavigate, showMobile, showMobileNav, hideMobileNav } = useHeader(headerRef);
	return (
		<header ref={ref}>
			<div className='logo'>
				{/* firedwood<span>.</span>de */}
				<img src='/logo2.svg' />
				{/* <div>
					<div>Fired</div>
					<div>wood</div>
				</div> */}
			</div>
			<ul className='main-nav'>
				{menu.map((el) => {
					return (
						<NavElement
							key={el.name}
							name={el.name}
							link={el.link}
							navigate={() => {
								handleNavigate(el.link);
							}}
						/>
					);
				})}
			</ul>
			{showMobile && <MobileNav hide={hideMobileNav} menu={menu} handleNavigate={handleNavigate} />}
			<div className='hamburger' onClick={showMobileNav}>
				<div />
				<div />
				<div />
			</div>
		</header>
	);
}
