const SET_PRODUCT = 'product/setProduct';
const CLEAR_PRODUCT = 'product/clearProduct';

export default (state = false, action) => {
	let data;
	switch (action.type) {
		case SET_PRODUCT:
			return { ...action.payload };
		case CLEAR_PRODUCT:
			return false;
		default:
			return state;
	}
};

const setProduct = (payload) => {
	return {
		type: SET_PRODUCT,
		payload: payload,
	};
};

const clearProduct = () => {
	return {
		type: CLEAR_PRODUCT,
	};
};

export { setProduct, clearProduct };
