import React from 'react';
import './index.css';
import useProducts from '../../Application/Controller/products';
import { useDispatch, useSelector } from 'react-redux';
import { setProductImageLoaded } from '../../Application/Redux/Reducers/lifecycle';

export default function Products() {
	const products = useSelector((state) => state.products);
	const dispatch = useDispatch();
	const { addToRefArray } = useProducts();

	return (
		<div className='products'>
			{products &&
				products.map((product) => {
					return (
						<div key={product.id} ref={addToRefArray} className='image-ref'>
							<div className='product-image' key={product.id}>
								<div className='image-shadow' />
								{/* <div className='product-image-label'>{product.name}</div> */}
								<img
									alt={`${product.image.url}`}
									onLoad={() => {
										dispatch(setProductImageLoaded());
									}}
									src={`${process.env.REACT_APP_BASE_URL}${product.image.formats.medium.url}`}
								/>
								<div className='label-2'>{product.name}</div>
							</div>
						</div>
					);
				})}
		</div>
	);
}
