import React from 'react';
import './index.css';
import useProjects from '../../Application/Controller/projects';
import { useDispatch, useSelector } from 'react-redux';
import { setProjectImageLoaded } from '../../Application/Redux/Reducers/lifecycle';

export default function Projects() {
	const dispatch = useDispatch();
	const projects = useSelector((state) => state.projects);
	const { addToRefArray2, handleNavigate } = useProjects();
	return (
		<section className='projects'>
			{projects &&
				projects.map((project) => {
					return (
						<div
							onClick={() => {
								handleNavigate(project.id);
							}}
							key={project.id}
							ref={addToRefArray2}
							className='project-image image-ref '>
							<div className='image-shadow' />
							<div className='project-image-label'>{project.name}</div>
							<img
								onLoad={() => {
									dispatch(setProjectImageLoaded());
								}}
								src={`${process.env.REACT_APP_BASE_URL}${project.image.url}`}
							/>
						</div>
					);
				})}
		</section>
	);
}
