import React from 'react';
import './mobileNav.css';
import { createPortal } from 'react-dom';
import NavElement from './navElement';
import Footer from '../Footer';

export default function MobileNav({ hide, menu, handleNavigate }) {
	return createPortal(
		<div className='mobile-nav'>
			<div className='shadow' onClick={hide}></div>

			<ul>
				<div className='logo'>firedwood.de</div>
				<div className='line' />
				{menu.map((el) => {
					return (
						<NavElement
							key={el.name}
							name={el.name}
							navigate={() => {
								hide();
								handleNavigate(el.link);
							}}
						/>
					);
				})}

				<Footer />
			</ul>
		</div>,
		document.getElementById('mobile-nav')
	);
}
