import React from 'react';
import './index.css';
import useContact from '../../Application/Controller/contact';
import { useSelector } from 'react-redux';

export default function Contact() {
	const contact = useSelector((state) => state.pages.contact);
	useContact();
	return (
		<div className='contact'>
			{contact && (
				<div className='contact-container'>
					<div className='contact-image'>
						<img src={`${process.env.REACT_APP_BASE_URL}${contact.image.url}`} />
					</div>
					<div className='contact-content'>
						<div className='c-left'>{contact.text}</div>
						<div className='c-right'>
							<div className='avatar-image'>
								<img src={`${process.env.REACT_APP_BASE_URL}${contact.imageAvatar.url}`} />
							</div>
							<div className='avatar-text'>{contact.shortText}</div>
						</div>
					</div>
				</div>
			)}
		</div>
	);
}
