import React, { useRef } from 'react';
import './index.css';
import useFooter from '../../Application/Controller/footer';

export default function Footer() {
	const footerRef = useRef();
	const { mw } = useFooter(footerRef);
	return (
		<footer ref={footerRef} className={mw ? 'mw' : ''}>
			<div className='footer-1st'>
				<div>datenschutz</div>
				<div>impressum</div>
				<div>agb</div>
			</div>
			<div className='footer-2nd'> Cyprian Lewandowski 2023</div>
		</footer>
	);
}
