const SET_ABOUT = 'pagesData/setAbout';
const CLEAR_ABOUT = 'pagesData/clearAbout';
const SET_CONTACT = 'pagesData/setContact';
const CLEAR_CONTACT = 'pagesData/clearContact';

export default (state = { about: false, contact: false }, action) => {
	switch (action.type) {
		case SET_ABOUT:
			return { ...state, about: action.payload };
		case CLEAR_ABOUT:
			return { ...state, about: false };
		case SET_CONTACT:
			return { ...state, contact: action.payload };
		case CLEAR_CONTACT:
			return { ...state, contact: false };
		default:
			return state;
	}
};

const setAbout = (payload) => {
	return {
		type: SET_ABOUT,
		payload: payload,
	};
};

const clearAbout = () => {
	return {
		type: CLEAR_ABOUT,
	};
};

const setContact = (payload) => {
	return {
		type: SET_CONTACT,
		payload: payload,
	};
};

const clearContact = () => {
	return {
		type: CLEAR_CONTACT,
	};
};

export { setAbout, clearAbout, setContact, clearContact };
