const SET_PROJECT = 'project/setProject';
const CLEAR_PROJECT = 'projkect/clearProject';

export default (state = false, action) => {
	let data;
	switch (action.type) {
		case SET_PROJECT:
			console.log('action.payload: ', action.payload);
			return { ...action.payload };
		case CLEAR_PROJECT:
			return false;
		default:
			return state;
	}
};

const setProject = (payload) => {
	return {
		type: SET_PROJECT,
		payload: payload,
	};
};

const clearProject = () => {
	return {
		type: CLEAR_PROJECT,
	};
};

export { setProject, clearProject };
