const SET_PAGE_LOADING = 'lifecycle/setPageLoading';
const SET_PAGE_LOADED = 'lifecycle/setPageLoaded';
const SET_HEADER_LOADED = 'lifecycle/setHeaderLoaded';
const SET_MAIN_PAGE_LOADED = 'lifecycle/setMainPageLoaded';
const SET_FOOTER_LOADED = 'lifecycle/setFooterLoaded';
const SET_CHANGING_PAGE = 'lifecycle/setChangingPage';
const SET_PRODUCTS_LENGTH = 'lifecycle/setProductsLength';
const SET_PROJECTS_LENGTH = 'lifecycle/setProjectsLength';
const SET_PRODUCT_IMAGE_LOADED = 'lifecycle/setProductImageLoaded';
const SET_PROJECT_IMAGE_LOADED = 'lifecycle/setProjectImageLoaded';
const SET_PRODUCT_GALLERY_LOADED = 'lifecycle/setProductGalleryLoaded';
const SET_PROJECT_GALLERY_LOADED = 'lifecycle/setProjectGalleryLoaded';
const CLEAR_PROJECT_GALLERY = 'lifecycle/clearProjectGallery';
const CLEAR_PRODUCTS_GALLERY = 'lifecycle/clearProducts/Gallery';

export default function lifecycle(
	state = {
		pageLoading: false,
		pageLoaded: false,
		changingPage: false,
		componentsLoaded: {
			headerLoaded: false,
			mainPageLoaded: false,
			footerLoaded: false,
		},
		images: {
			products: { length: 0, loaded_images: 0, loaded: false },
			projects: { length: 0, loaded_images: 0, loaded: false },
		},
	},
	action
) {
	let data;
	switch (action.type) {
		case SET_PAGE_LOADING:
			return { ...state, pageLoaded: false, pageLoading: true };
		case SET_PAGE_LOADED:
			return { ...state, pageLoading: false, pageLoaded: true };
		case SET_HEADER_LOADED:
			data = { ...state, componentsLoaded: { ...state.componentsLoaded, headerLoaded: true } };
			if (checkPageLoaded(Object.values(data.componentsLoaded))) {
				data.pageLoading = false;
				data.pageLoaded = true;
			}
			return { ...data };
		case SET_FOOTER_LOADED:
			data = { ...state, componentsLoaded: { ...state.componentsLoaded, footerLoaded: true } };
			if (checkPageLoaded(Object.values(data.componentsLoaded))) {
				data.pageLoading = false;
				data.pageLoaded = true;
			}
			return { ...data };
		case SET_MAIN_PAGE_LOADED:
			data = { ...state, componentsLoaded: { ...state.componentsLoaded, mainPageLoaded: true } };
			if (checkPageLoaded(Object.values(data.componentsLoaded))) {
				data.pageLoading = false;
				data.pageLoaded = true;
			}
			return { ...data };
		case SET_CHANGING_PAGE:
			return { ...state, changingPage: action.payload };

		case SET_PRODUCTS_LENGTH:
			data = { ...state, images: { ...state.images, products: { ...state.images.products, length: action.payload } } };
			return { ...data };
		case SET_PROJECTS_LENGTH:
			data = { ...state, images: { ...state.images, projects: { ...state.images.projects, length: action.payload } } };
			return { ...data };
		case SET_PRODUCT_IMAGE_LOADED:
			data = {
				...state,
				images: { ...state.images, products: { ...state.images.products, loaded_images: state.images.products.loaded_images + 1 } },
			};
			if (data.images.products.length === data.images.products.loaded_images) {
				data.images.products.loaded = true;
			}
			return { ...data };
		case SET_PROJECT_IMAGE_LOADED:
			data = {
				...state,
				images: { ...state.images, projects: { ...state.images.projects, loaded_images: state.images.projects.loaded_images + 1 } },
			};
			if (data.images.projects.length === data.images.projects.loaded_images) {
				data.images.projects.loaded = true;
			}
			return { ...data };
			return { ...data };
		case SET_PRODUCT_GALLERY_LOADED:
			data = {
				...state,
				images: { ...state.images, products: { ...state.images.products, loaded: true } },
			};
			return { ...data };
		case SET_PROJECT_GALLERY_LOADED:
			data = {
				...state,
				images: { ...state.images, projects: { ...state.images.projects, loaded: true } },
			};
			return { ...data };
		case CLEAR_PRODUCTS_GALLERY:
			data = { ...state, images: { ...state.images, products: { length: 0, loaded_images: 0, loaded: false } } };
			return { ...data };
		case CLEAR_PROJECT_GALLERY:
			data = { ...state, images: { ...state.images, projects: { length: 0, loaded_images: 0, loaded: false } } };
			return { ...data };
		default:
			return state;
	}
}

const clearProductsGallery = () => {
	return {
		type: CLEAR_PRODUCTS_GALLERY,
	};
};

const clearProjectsGallery = () => {
	return {
		type: CLEAR_PROJECT_GALLERY,
	};
};

const setProjectGalleryLoaded = () => {
	return { type: SET_PROJECT_GALLERY_LOADED };
};

const setProductGalleryLoaded = () => {
	return { type: SET_PRODUCT_GALLERY_LOADED };
};

const setProductImageLoaded = () => {
	return { type: SET_PRODUCT_IMAGE_LOADED };
};

const setProjectImageLoaded = () => {
	return { type: SET_PROJECT_IMAGE_LOADED };
};

const setProductsLength = (payload) => {
	return {
		type: SET_PRODUCTS_LENGTH,
		payload: payload,
	};
};

const setProjectsLength = (payload) => {
	return {
		type: SET_PROJECTS_LENGTH,
		payload: payload,
	};
};

const checkPageLoaded = (componentsValues) => {
	let res = true;
	componentsValues.forEach((el) => {
		if (!el) res = false;
	});

	return res;
};

const setheaderLoaded = () => {
	return {
		type: SET_HEADER_LOADED,
	};
};

const setMainPageLoaded = () => {
	return {
		type: SET_MAIN_PAGE_LOADED,
	};
};

const setFooterLoaded = () => {
	return {
		type: SET_FOOTER_LOADED,
	};
};

const setPageLoading = () => {
	return {
		type: SET_PAGE_LOADING,
	};
};

const setPageLoaded = () => {
	return {
		type: SET_PAGE_LOADED,
	};
};

const setChangingPage = (payload) => {
	return {
		type: SET_CHANGING_PAGE,
		payload: payload,
	};
};

export {
	setProductGalleryLoaded,
	setProjectGalleryLoaded,
	clearProductsGallery,
	clearProjectsGallery,
	setProductImageLoaded,
	setProjectImageLoaded,
	setPageLoading,
	setPageLoaded,
	setheaderLoaded,
	setMainPageLoaded,
	setFooterLoaded,
	setChangingPage,
	setProductsLength,
	setProjectsLength,
};
