const SET_PRODUCTS = 'products/setProducts';
const CLEAR_PRODUCTS = 'products/clearProducts';

export default (state = false, action) => {
	switch (action.type) {
		case SET_PRODUCTS:
			return [...action.payload];
		case CLEAR_PRODUCTS:
			return false;
		default:
			return state;
	}
};

const setProducts = (payload) => {
	return {
		type: SET_PRODUCTS,
		payload: payload,
	};
};

const clearProducts = () => {
	return {
		type: CLEAR_PRODUCTS,
	};
};

export { setProducts, clearProducts };
