import useFetch from '../Hooks/fetch';
import { useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { clearProjectsGallery, setChangingPage, setMainPageLoaded, setProjectsLength } from '../Redux/Reducers/lifecycle';

import { clearProjects, setProjects } from '../Redux/Reducers/projects';
import { gsap } from 'gsap';
import { useNavigate } from 'react-router-dom';

export default function useProjects() {
	const dispatch = useDispatch();
	const navigate = useNavigate();
	const { getData } = useFetch();
	const projects = useSelector((state) => state.projects);

	const loaded = useSelector((state) => state.lifecycle.images.projects.loaded);
	const imagesArray2 = useRef([]);
	imagesArray2.current = [];
	useEffect(() => {
		getData('/projects?populate=*').then((res) => {
			if (!res.error) {
				dispatch(
					setProjects(
						res.data.map((el) => {
							return {
								...el.attributes,
								id: el.id,
								image: { ...el.attributes.image.data.attributes, id: el.attributes.image.data.id },
							};
						})
					)
				);
				dispatch(setMainPageLoaded());
				setTimeout(() => {
					dispatch(setChangingPage(false));
				}, 300);
			} else {
				navigate('/error');
			}
		});

		return () => {
			dispatch(clearProjects());
			dispatch(clearProjectsGallery());
		};
	}, []);

	useEffect(() => {
		loaded && console.log('loaded');
		loaded && console.log('imagesarrayL: ', imagesArray2.current);
		gsap.set(imagesArray2.current, {
			y: -45,
			x: -25,
		});
		loaded && gsap.to(imagesArray2.current, { opacity: 1, y: 0, x: 0, delay: 0.4, duration: 0.15, stagger: { each: 0.03 }, ease: 'circ' });
	}, [loaded]);

	useEffect(() => {
		projects && dispatch(setProjectsLength(projects.length));
	}, [projects]);

	const addToRefArray2 = (el) => {
		if (el && !imagesArray2.current.includes(el)) {
			imagesArray2.current.push(el);
		}
	};

	const handleNavigate = (id) => {
		dispatch(setChangingPage(true));
		navigate(`/project/${id}`);
	};

	return { addToRefArray2, handleNavigate };
}
