import React from 'react';
import { useSelector } from 'react-redux';
import './index.css';
import useProject from '../../Application/Controller/project';

export default function Project() {
	useProject();
	const project = useSelector((state) => state.project);
	return (
		<div className='project'>
			<h1>{project.name}</h1>
			<p>{project.description}</p>
			<div className='project-head-image'>
				{project && <img alt={project.name} src={`${process.env.REACT_APP_BASE_URL}${project.image.formats.large.url}`} />}
			</div>

			{project &&
				project.gallery &&
				project.gallery.map((el) => {
					return (
						<div className='project-g-image'>
							<img alt={`${project.name}${el.url}`} src={`${process.env.REACT_APP_BASE_URL}${el.url}`} />
						</div>
					);
				})}
		</div>
	);
}
